.header {
  display: flex;
  background-color: transparent;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 5%;
  color: #fff;
  position: relative;
  z-index: 100;

  .logo {
    height: 3rem;
    flex-shrink: 0;

    img {
      height: 100%;
      width: 100%;
    }
  }

  .nav {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    position: absolute;
    top: 100%;
    left: 0;
    z-index: 20;
    background-color: $purple;
    padding: 2rem;
    gap: 1rem;

    transform: scaleY(0);
    transform-origin: top;
    transition: 0.3s;

    &.open {
      transform: scaleY(1);
    }

    .left {
      display: flex;
      width: 100%;
      justify-content: center;
      align-items: center;
      gap: 1rem;
      flex-direction: column;

      Link {
        display: block;

        &:hover {
          color: $red;
        }
      }
    }
  }

  button {
    align-items: center;
    background: linear-gradient(265.86deg, #d6882f 8.45%, #fbb961 101.33%);
  }

  .menu-toggle {
    padding: 0.25rem;
    font-size: 2rem;

    cursor: pointer;
  }

  @media (min-width: $ipad) {
    .nav {
      position: relative;
      top: 0;
      flex-direction: row;
      background-color: transparent;
      transform: scaleY(1);
      padding: 0;

      .left {
        gap: 2rem;
        flex-direction: row;
      }
    }
  }
}
