#home {
  height: auto;

  .top-section {
    height: 100%;
    // background: linear-gradient(180deg, #131313 0%, #18102c 100%);
    background-size: cover;
    position: relative;

    .discoLines {
      position: absolute;
      z-index: 1;
      pointer-events: none;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url("../../assets/images/discolines.png");
      background-size: cover;
      background-position: center center;
    }

    .hero {
      padding: 2rem 5%;
      margin-top: -6rem;
      color: #fff;
      display: grid;
      height: 100vh;
      grid-template-columns: 100%;
      grid-template-rows: repeat(2, 1fr);
      align-items: center;

      h2 {
        font-size: 4rem;
        line-height: 1;
      }

      > * {
        justify-content: center;
        position: relative;
        z-index: 11;
      }

      .imgContainer {
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;

        img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          object-fit: contain;
          aspect-ratio: 16 / 9;
        }
      }

      @media (min-width: $ipad) {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 100%;

        h2 {
          font-size: 5.3rem;
        }
      }
    }
  }

  .section {
    &.events {
      position: relative;
      height: 100%;
      min-height: 100vh;
      background: url("../../assets/images/Categories-BG.png") center center
        no-repeat;
      background-size: cover;
      background-color: $black-200;
      padding: 2rem 5%;

      text-align: center;

      display: flex;
      flex-direction: column;
      justify-content: center;

      gap: 1rem;

      .cards {
        padding-top: 3rem;

        .card {
          background-color: rgba($white, 1);
          padding: 10px;
          display: flex;
          align-items: center;
          gap: 10px;

          border-radius: 0.35rem;
          z-index: 10 !important;
          top: 1rem;

          text-align: left;

          font-size: 1.35rem;
          font-weight: 700;

          box-shadow: -10px -10px rgba(242, 242, 242, 0.529);
        }
      }
    }

    &.how-it-works {
      padding: 2rem 5%;
      background-image: url("../../assets/images/Features-BG.png");
      background-size: cover;
      background-position: center center;
      background-repeat: no-repeat;

      .button {
        padding: 1.25rem 2rem;
        align-items: center;
        background-color: $purple;
        color: $white;

        .icon {
          padding-right: 1rem;
          font-size: 2rem;
        }
      }

      img {
        height: 120%;
      }
    }

    &.words-of-mouth {
      padding: 2rem 5%;
      background-color: $black;

      button {
        background: linear-gradient(265.86deg, #d6882f 8.45%, #fbb961 101.33%);
      }
    }

    &.monetize {
      .grid-layout {
        padding: 2rem 5%;
        gap: 2rem;

        &:nth-child(1) {
          background-color: $black-200;
        }
      }

      button {
        background: linear-gradient(265.86deg, #d6882f 8.45%, #fbb961 101.33%);
      }
    }

    &.nest {
      padding: 2rem 5%;
      background-color: $black-200;

      .imgContainer {
        height: 4rem;

        img {
          height: 100%;
          width: auto;
        }
      }

      > div {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        justify-content: center;
        align-items: center;
        text-align: center;
      }
    }

    .grid-layout {
      .imgContainer-1 {
        order: 2;
      }

      .grid-text {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 1rem;

        p {
          color: $white-200;
          line-height: 1.36;
        }
      }

      div {
      }
    }

    h2 {
      font-size: 2.5rem;
      line-height: 1.4;
    }
  }

  .grid-layout {
    display: grid;
    grid-template-columns: 100%;
    gap: 1rem;
    align-items: center;

    .imgContainer {
      height: 100%;
    }
  }

  .intro {
    text-align: center;
    padding: 1rem;
    max-width: $laptop;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;

    p {
      color: $white-200;
      line-height: 1.36;
    }
  }

  @media (min-width: $ipad) {
    .section {
      &.events {
      }

      .grid-layout {
        .imgContainer-1 {
          order: initial;
        }
      }
    }

    .grid-layout {
      grid-template-columns: repeat(2, 1fr);
      gap: 2rem;
    }
  }
}
