.accordions {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: 100%;
}

.accordion {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  height: 2rem;
  background-color: #141414;
  border-radius: 0.65rem;

  padding: 2rem;

  &__title {
    font-size: 1.15rem;
    font-weight: 600;
    display: flex;
    align-items: center;
  }

  &__body {
    font-size: 1rem;
    opacity: 0;
    transform: scaleY(0);
    transform-origin: top;
    padding: 1rem;
  }

  .icon {
    margin-right: 1rem;
    transform: rotate(-90deg);
  }

  &.active {
    height: max-content;

    .accordion__body {
      opacity: 1;
      transform: scaleY(1);
    }

    .icon {
      transform: rotate(0deg);
    }
  }
}

@media (min-width: $ipad) {
  .accordions {
    grid-template-columns: repeat(2, 1fr);
  }
}
